const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://xcx.huitengzx.com/api/v2', // 
		method: 'POST',
		// jsonJSON.parse()
		dataType: 'json',
		showLoading: true, // loading
		loadingText: '...', // loading
		loadingTime: 800, // ms
		originalData: false, // 
		loadingMask: true, // loading
		// 
		header: {
			'content-type': 'application/json;charset=UTF-8'
		},
	});
	
	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// ......
		return config;
	}
	
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		if(res.result == 1) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res;
		} else if(res.result == -2) {
			// 201token
			vm.$u.toast('');
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/login/login')
			}, 1500)
			return false;
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return false;
		}
	}
}

export default {
	install
}